<script>
import { reactive } from "vue";
import axios from "axios";

export default {
    props: {
        getDate: {
            type: Function,
            default: () => {}
        }
    },
    setup(props) {
        const editPwdUrl =
            "https://script.google.com/macros/s/AKfycbzRgKHX0eSD0ZY_dvSxvXnUodWdGw4y_NLbzJvlLOM2bh5wjjIp/exec";
        const editPwdObj = reactive({
            action: "modify_pwd",
            time: "2020/11/16 20:00:00",
            id: "",
            pwd: "",
            confirmation_pwd: "",
        });

        const modifyPwdSend = (obj) => {
            editPwdObj.time = props.getDate();

            axios
            .post(editPwdUrl, JSON.stringify(obj))
            .then(function(res) {
                swal(res.data.res_content);
            })
            .catch(function(error) {
                console.log(error);
            });
        }

        return { editPwdObj, modifyPwdSend };
    },
};
</script>

<template>
    <div class="modify-pwd">
        <div class="container">
            <form class="panel-login">
                <label class="form-item">
                    <p class="form-title">帳號:</p>
                    <input
                        type="text"
                        class="form-control"
                        placeholder="請輸入帳號"
                        name="account"
                        required
                        v-model.lazy.trim="editPwdObj.id"
                    />
                </label>
                <label class="form-item">
                    <p class="form-title">密碼:</p>
                    <input
                        type="password"
                        class="form-control"
                        placeholder="請輸入密碼"
                        name="pwd"
                        required
                        v-model.lazy.trim="editPwdObj.pwd"
                    />
                </label>
                <label class="form-item">
                    <p class="form-title">密碼確認:</p>
                    <input
                        type="password"
                        class="form-control"
                        placeholder="請輸入密碼"
                        name="pwd_confirm"
                        required
                        v-model.lazy.trim="editPwdObj.confirmation_pwd"
                    />
                </label>
                <button class="btn btn-primary w-100" @click.prevent="modifyPwdSend(editPwdObj)">送出</button>
            </form>
        </div>
    </div>
</template>

<style lang="scss" scoped></style>
