<script>
import { onMounted, reactive } from "vue";
import axios from "axios";
export default {
    props: {
        getDate: {
            type: Function,
            default: () => {}
        }
    },
    setup(props) {
        const editInfoUrl =
            "https://script.google.com/macros/s/AKfycbzRgKHX0eSD0ZY_dvSxvXnUodWdGw4y_NLbzJvlLOM2bh5wjjIp/exec";
        const editInfoObj = reactive({
            action: "modify_member_info",
            time: "2020/11/16 20:00:00",
            id: "",
            name: "",
            phone: "",
            birthday: "",
            address: "",
            gender: "",
            about: ""
        });

        const modifyInfoSend = () => {
            editInfoObj.time = props.getDate();

            axios
            .post(editInfoUrl, JSON.stringify(editInfoObj))
            .then(function(res) {
                swal(res.data.res_content);
            })
            .catch(function(error) {
                console.log(error);
            });
        }

        onMounted(() => {
            const isLogin = localStorage.getItem('isLogin');

            if(isLogin == 1) {
                const getData = JSON.parse(localStorage.getItem('loggedInData'));

                editInfoObj.id = getData.id;
                editInfoObj.name = getData.name;
                editInfoObj.phone = getData.phone;
                editInfoObj.birthday = getData.birthday.split('T')[0];
                editInfoObj.address = getData.address;
                editInfoObj.gender = getData.gender;
                editInfoObj.about = getData.about;
            }
        })

        return{ editInfoObj, modifyInfoSend };
    },
};
</script>

<template>
    <div class="modify-member-info">
        <div class="container">
            <form class="panel-login">
                <label class="form-item">
                    <p class="form-title">帳號:</p>
                    <input
                        type="text"
                        class="form-control"
                        placeholder="請輸入帳號"
                        name="account"
                        disabled
                        v-model="editInfoObj.id"
                    />
                </label>
                <label class="form-item">
                    <p class="form-title">姓名:</p>
                    <input
                        type="text"
                        class="form-control"
                        placeholder="請輸入姓名"
                        name="name"
                        disabled
                        v-model="editInfoObj.name"
                    />
                </label>
                <label class="form-item">
                    <p class="form-title">電話:</p>
                    <input
                        type="tel"
                        class="form-control"
                        placeholder="請輸入手機"
                        name="phone"
                        required
                        v-model.lazy.trim="editInfoObj.phone"
                    />
                </label>
                <label class="form-item">
                    <p class="form-title">生日:</p>
                    <input type="date" class="form-control" name="birth" required v-model.lazy="editInfoObj.birthday" />
                </label>
                <label class="form-item">
                    <p class="form-title">地址:</p>
                    <input
                        type="text"
                        class="form-control"
                        placeholder="請輸入地址"
                        name="address"
                        required
                        v-model.lazy.trim="editInfoObj.address"
                    />
                </label>
                <div class="form-item">
                    <p class="form-title">性別:</p>
                    <label>
                        <input
                            type="radio"
                            name="gender"
                            id=""
                            value="0"
                            required
                            v-model="editInfoObj.gender"
                        />
                        男
                    </label>
                    <label>
                        <input
                            type="radio"
                            name="gender"
                            id=""
                            value="1"
                            required
                            v-model="editInfoObj.gender"
                        />
                        女
                    </label>
                </div>
                <label class="form-item">
                    <p class="form-title">自介:</p>
                    <textarea
                        id=""
                        class="form-control"
                        cols="30"
                        rows="10"
                        name="info"
                        required
                        v-model.lazy.trim="editInfoObj.about"
                    ></textarea>
                </label>
                <button class="btn btn-primary w-100" @click.prevent="modifyInfoSend">送出</button>
            </form>
        </div>
    </div>
</template>

<style lang="scss" scoped></style>
