<script>
import { reactive } from "vue";
import axios from "axios";
export default {
    props: {
        getDate: {
            type: Function,
            default: () => {}
        }
    },
    setup(props) {
        const loginUrl =
            "https://script.google.com/macros/s/AKfycbzRgKHX0eSD0ZY_dvSxvXnUodWdGw4y_NLbzJvlLOM2bh5wjjIp/exec";
        const loginDataObj = reactive({
            action: "login",
            time: "2020/11/16 20:00:00",
            id: "",
            pwd: "",
        });

        const loginSend = (obj) => {
            loginDataObj.time = props.getDate();

            axios
                .post(loginUrl, JSON.stringify(loginDataObj))
                .then(function(res) {
                    swal(res.data.res_content);
                    localStorage.setItem('isLogin', res.data.res_code);
                    localStorage.setItem('loggedInData', JSON.stringify(res.data.res_data));
                })
                .catch(function(error) {
                    console.log(error);
                });
        };

        return { loginDataObj, loginSend };
    },
};
</script>

<template>
    <div class="login">
        <div class="container">
            <form class="panel-login">
                <label class="form-item">
                    <p class="form-title">帳號:</p>
                    <input
                        type="text"
                        class="form-control"
                        placeholder="請輸入帳號"
                        name="account"
                        required
                        v-model.lazy.trim="loginDataObj.id"
                    />
                </label>
                <label class="form-item">
                    <p class="form-title">密碼:</p>
                    <input
                        type="password"
                        class="form-control"
                        placeholder="請輸入密碼"
                        name="pwd"
                        required
                        v-model.lazy.trim="loginDataObj.pwd"
                    />
                </label>
                <button
                    class="btn btn-primary w-100"
                    @click.prevent="loginSend(loginDataObj)"
                >
                    登入
                </button>
            </form>
        </div>
    </div>
</template>

<style lang="scss" scoped></style>
